import React, { Fragment } from 'react';
import Link from 'next/link';

const Navigation = () => {
  return (
    <Fragment>
      <ul>
        <Link href="/">
          <a>Главная</a>

          <a>Регистрация ООО</a>
          <a>Регистрация ФЛП</a>
          <a>О нас</a>
        </Link>
        <Link href="/accountant">
          <a>Бухгалтерские услуги</a>
        </Link>
      </ul>
      <style jsx>{`
        ul {
          background-color: #74d9f2;
          height: 50px;
        }

        a {
          font-family: 'Montserrat';
          font-size: 18px;
          color: #000;
          line-height: 50px;
        }

        `}</style>
    </Fragment>
  );
}

export default Navigation;
